"use client"

import Link from "next/link"
import { useState, useEffect } from "react"
import { Menu, X, Facebook, Twitter, Instagram, Linkedin, Youtube, ChevronDown, ChevronRight } from "lucide-react"
import Image from "next/image"
import { getMenuHierarchy } from "@/lib/teamService"

interface MenuItem {
  id: string
  title: string
  type: "main" | "submenu"
  parentId: string | null
  order: number
  level: number
  image?: string
  children?: MenuItem[]
}

export function Navigation() {
  const [isOpen, setIsOpen] = useState(false)
  const [teamMenuOpen, setTeamMenuOpen] = useState(false)
  const [menuItems, setMenuItems] = useState<MenuItem[]>([])
  const [loading, setLoading] = useState(true)
  const [expandedMobileMenus, setExpandedMobileMenus] = useState<Set<string>>(new Set())

  useEffect(() => {
    const loadData = async () => {
      try {
        const menus = await getMenuHierarchy()
        setMenuItems(menus)
      } catch (error) {
        console.error("Error loading navigation data:", error)
      } finally {
        setLoading(false)
      }
    }

    loadData()
  }, [])

  // Toggle mobile menu expansion
  const toggleMobileMenu = (menuId: string) => {
    const newExpanded = new Set(expandedMobileMenus)
    if (newExpanded.has(menuId)) {
      newExpanded.delete(menuId)
    } else {
      newExpanded.add(menuId)
    }
    setExpandedMobileMenus(newExpanded)
  }

  // Render desktop team menu
  const renderDesktopTeamMenu = () => {
    if (loading) {
      return (
        <div className="px-4 py-3 text-sm text-gray-500">
          Loading...
        </div>
      )
    }

    if (menuItems.length === 0) {
      return (
        <div className="px-4 py-3 text-sm text-gray-500">
          No team menus available
        </div>
      )
    }

    return menuItems.map((menu) => (
      <div key={menu.id} className="mb-2 last:mb-0">
        {/* Main Menu Header */}
        <Link
          href={`/team/${menu.id}`}
          className="block px-4 py-2 font-semibold text-gray-900 hover:bg-gray-50 rounded"
          onClick={() => setTeamMenuOpen(false)}
        >
          {menu.title}
        </Link>

        {/* Submenus */}
        {menu.children && menu.children.length > 0 && (
          <div className="ml-4 mt-1 space-y-1">
            {menu.children.map((child) => (
              <div key={child.id}>
                {/* Submenu Link */}
                <Link
                  href={`/team/${child.id}`}
                  className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-50 rounded"
                  onClick={() => setTeamMenuOpen(false)}
                >
                  {child.title}
                </Link>

                {/* Grandchildren */}
                {child.children && child.children.length > 0 && (
                  <div className="ml-4 mt-1 space-y-1">
                    {child.children.map((grandchild) => (
                      <Link
                        key={grandchild.id}
                        href={`/team/${grandchild.id}`}
                        className="block px-4 py-1 text-xs text-gray-600 hover:bg-gray-50 rounded"
                        onClick={() => setTeamMenuOpen(false)}
                      >
                        {grandchild.title}
                      </Link>
                    ))}
                  </div>
                )}
              </div>
            ))}
          </div>
        )}
      </div>
    ))
  }

  // Recursive function to render mobile team menu
  const renderMobileTeamMenu = (menus: MenuItem[], level = 0): JSX.Element[] => {
    return menus.map((menu) => {
      const isExpanded = expandedMobileMenus.has(menu.id)
      const hasChildren = menu.children && menu.children.length > 0

      return (
        <div key={menu.id} className={level > 0 ? 'ml-4' : ''}>
          {/* Menu Header */}
          <div className="flex items-center justify-between py-2">
            <Link
              href={`/team/${menu.id}`}
              className={`flex-1 text-sm font-medium text-gray-700 hover:text-black ${
                level > 0 ? 'pl-2' : ''
              }`}
              onClick={() => setIsOpen(false)}
            >
              <span>
                {level > 0 ? '— '.repeat(level) : ''}
                {menu.title}
              </span>
            </Link>
            
            {hasChildren && (
              <button
                onClick={(e) => {
                  e.preventDefault()
                  e.stopPropagation()
                  toggleMobileMenu(menu.id)
                }}
                className="p-1 hover:bg-gray-100 rounded"
              >
                <ChevronRight className={`w-4 h-4 transition-transform ${isExpanded ? 'rotate-90' : ''}`} />
              </button>
            )}
          </div>

          {/* Expanded Content - Children */}
          {isExpanded && hasChildren && menu.children && (
            <div className="ml-4 border-l border-gray-200 pl-4">
              {renderMobileTeamMenu(menu.children, level + 1)}
            </div>
          )}
        </div>
      )
    })
  }

  const links = [
    { href: "/", label: "Home" },
    { href: "/about", label: "About" },
    { href: "/our-work", label: "Our Work" },
    { href: "/contact", label: "Contact Us" },
  ]

  const socialLinks = [
    { 
      href: "https://facebook.com", 
      label: "Facebook", 
      icon: <Facebook className="w-4 h-4" />,
      ariaLabel: "Visit our Facebook page"
    },
    { 
      href: "https://twitter.com", 
      label: "Twitter", 
      icon: <Twitter className="w-4 h-4" />,
      ariaLabel: "Visit our Twitter page"
    },
    { 
      href: "https://instagram.com", 
      label: "Instagram", 
      icon: <Instagram className="w-4 h-4" />,
      ariaLabel: "Visit our Instagram page"
    },
    { 
      href: "https://linkedin.com", 
      label: "LinkedIn", 
      icon: <Linkedin className="w-4 h-4" />,
      ariaLabel: "Visit our LinkedIn page"
    },
    { 
      href: "https://youtube.com", 
      label: "YouTube", 
      icon: <Youtube className="w-4 h-4" />,
      ariaLabel: "Visit our YouTube channel"
    },
  ]

  return (
    <nav className="sticky top-0 z-50 w-full bg-white border-b border-gray-200">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-20">
          {/* Logo and Foundation Name */}
          <Link href="/" className="flex items-center gap-2 font-bold text-xl">
            <div className="w-8 h-8 bg-gray-900 rounded-full flex items-center justify-center">
              <Image src={"/images/asf-logo.png"} alt="logo" height={50} width={50}/>
            </div>
            <span className="hidden sm:inline text-gray-900">Aapka Sahyog Foundation</span>
          </Link>

          {/* Desktop Navigation */}
          <div className="hidden lg:flex gap-8 items-center">
            {links.map((link) => (
              <Link
                key={link.href}
                href={link.href}
                className="text-sm font-medium text-gray-700 hover:text-black transition-colors duration-200"
              >
                {link.label}
              </Link>
            ))}
            
            {/* Team Dropdown */}
            <div className="relative">
              <button
                onClick={() => setTeamMenuOpen(!teamMenuOpen)}
                className="flex items-center gap-1 text-sm font-medium text-gray-700 hover:text-black transition-colors duration-200"
              >
                Team
                <ChevronDown className={`w-4 h-4 transition-transform ${teamMenuOpen ? 'rotate-180' : ''}`} />
              </button>
              
              {teamMenuOpen && (
                <div className="absolute left-0 top-full mt-2 w-64 bg-white rounded-lg shadow-lg border border-gray-200 overflow-y-auto max-h-[80vh] z-50 p-2">
                  {renderDesktopTeamMenu()}
                </div>
              )}
            </div>
          </div>

          {/* Desktop Social Media and Donate Button */}
          <div className="hidden lg:flex items-center gap-4">
            <div className="flex items-center gap-2 mr-2">
              {socialLinks.map((social) => (
                <a
                  key={social.href}
                  href={social.href}
                  target="_blank"
                  rel="noopener noreferrer"
                  aria-label={social.ariaLabel}
                  className="p-2 text-gray-500 hover:text-black hover:bg-gray-100 rounded-full transition-colors duration-200"
                >
                  {social.icon}
                </a>
              ))}
            </div>
            
            <Link
              href="/get-involved"
              className="px-6 py-2 bg-gray-900 text-white rounded-lg font-semibold hover:bg-gray-800 transition-colors duration-200 whitespace-nowrap"
            >
              Donate Now
            </Link>
          </div>

          {/* Mobile Menu Button */}
          <button
            onClick={() => setIsOpen(!isOpen)}
            className="lg:hidden p-2 hover:bg-gray-100 rounded-lg transition-colors"
            aria-label={isOpen ? "Close menu" : "Open menu"}
          >
            {isOpen ? <X size={20} /> : <Menu size={20} />}
          </button>
        </div>

        {/* Mobile Navigation */}
        {isOpen && (
          <div className="lg:hidden pb-4 border-t border-gray-200">
            <div className="space-y-2 py-4">
              {links.map((link) => (
                <Link
                  key={link.href}
                  href={link.href}
                  className="block px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50 rounded-lg transition-colors"
                  onClick={() => setIsOpen(false)}
                >
                  {link.label}
                </Link>
              ))}
              
              {/* Mobile Team Menu */}
              <div className="px-4 py-2">
                <div className="text-sm font-medium text-gray-900 mb-2">Team</div>
                <div className="space-y-1">
                  {renderMobileTeamMenu(menuItems)}
                  
                  {menuItems.length === 0 && !loading && (
                    <div className="px-2 py-2 text-sm text-gray-500">
                      No team menus available
                    </div>
                  )}
                </div>
              </div>
            </div>

            {/* Mobile Social Media Links */}
            <div className="px-4 py-4 border-t border-gray-200">
              <p className="text-sm font-medium text-gray-500 mb-3">Follow Us</p>
              <div className="flex items-center gap-3">
                {socialLinks.map((social) => (
                  <a
                    key={social.href}
                    href={social.href}
                    target="_blank"
                    rel="noopener noreferrer"
                    aria-label={social.ariaLabel}
                    className="p-2 text-gray-500 hover:text-black hover:bg-gray-100 rounded-full transition-colors duration-200"
                    onClick={() => setIsOpen(false)}
                  >
                    {social.icon}
                  </a>
                ))}
              </div>
            </div>

            {/* Mobile Donate Button */}
            <div className="px-4 pt-2">
              <Link
                href="/get-involved"
                className="block w-full px-4 py-3 bg-gray-900 text-white rounded-lg font-semibold text-center hover:bg-gray-800 transition-colors"
                onClick={() => setIsOpen(false)}
              >
                Donate Now
              </Link>
            </div>
          </div>
        )}
      </div>
    </nav>
  )
}